tton, select, textarea {\n        font-family: 'Open Sans', sans-serif!important;\n        font-size: inherit;\n        line-height: inherit;\n    }\n\n    label {\n        font-weight: normal;\n        margin: auto;\n        display: inline-flex;\n    }\n    position: relative;\n    max-width: 1500px;\n    min-height: 100%;\n    font: 13px 'Open Sans', sans-serif;\n    line-height: 18px;\n\n    span.active {\n        background-color: gray;\n    }\n\n    h1 {\n        margin-bottom: 0;\n        height: 35px;\n        font-size: 33px;\n        font-weight: 400;\n        color: #404040;\n    }\n\n    .b_btn {\n        font-size: 14px;\n        outline: none;\n\n        &:disabled {\n            box-shadow: none;\n        }\n    }\n\n    html.rtl & :not(th).rtl-except {\n        direction: ltr;\n        text-align: right;\n    }\n\n    .canvas {\n        min-height: 50px;\n        background-color: #ffffff;\n        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.24), 0 0 2px 0 rgba(0, 0, 0, 0.12);\n        border-style: solid;\n        border-width: 0;\n        border-image-source: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.1) 5%, rgba(255, 255, 255, 0) 20%, rgba(255, 255, 255, 0));\n        border-image-slice: 1;\n        &.no-border {\n            border: none;\n            box-shadow: none;\n        }\n    }\n\n    a {\n        cursor: pointer;\n    }\n    .coming-soon_element a {\n        color: #9E9E9E !important;\n    }\n\n    .filters-actions-wrapper {\n        align-items: flex-end;\n        justify-content: space-between;\n    }\n\n    .mat-tooltip {\n        font-family: 'Open Sans';\n        font-size: 11px;\n        font-weight: normal;\n        font-style: normal;\n        font-stretch: normal;\n\n        &.white-space {\n            white-space: pre-wrap;\n        }\n\n        &.wide-tooltip {\n            max-width: 600px!important;\n        }\n        &.version-tooltip {\n            min-width: 200px;\n            white-space: pre-line;\n            background: rgb(34 32 32 / 90%);\n        }\n    }\n\n    .lineBreakable {\n        white-space: pre-line;\n    }\n\n    .i360-header {\n        display: flex;\n        align-items: baseline;\n        justify-content: space-between;\n        vertical-align: middle;\n        line-height: 1;\n\n        h1 {\n            height: initial;\n            margin-top: 0;\n            margin-bottom: 16px;\n        }\n    }\n\n    .i360-header-section {\n        display: flex;\n        width: 100%;\n        align-items: center;\n\n        i360-header {\n            flex-grow: 1;\n        }\n    }\n\n    .uppercase {\n        text-transform: uppercase;\n    }\n\n    .support {\n        display: inline-block;\n        width: 25px;\n        height: 24px;\n    }\n    .severity-icon {\n        display: inline-block;\n        @include rtl-margin-right(8px);\n        width: 8px;\n        height: 8px;\n        border-radius: 8px;\n        &.high {\n            background-color: $severity-red;\n        }\n\n        &.low {\n            background-color: $severity-green;\n        }\n\n        &.medium {\n            background-color: $severity-orange;\n        }\n\n        &.log {\n            background-color: #9a9a9a;\n        }\n\n        &.block {\n            background-color: $severity-orange;\n        }\n\n        &.kill {\n            background-color: $severity-red;\n        }\n    }\n    svg { // plesk, ?\n        vertical-align: baseline;\n    }\n    .text-uppercase {\n        text-transform: uppercase;\n    }\n}\n\n.cdk-visually-hidden\n{\n    display: none;\n}\n","$primary-color: #11cb84;\n$header-color: #1d1d1d;\n$focused-bg: rgba(153, 153, 153, 0.2);\n$pressed-bg: rgba(153, 153, 153, 0.4);\n$disabled-fg: rgba(0, 0, 0, 0.26);\n$scroll-color: #53b076;\n$gray-hover-bg: #f1f1f1;\n$table-hover-bg: #f8f9fb;\n$primary-hover-bg: #e9f6ee;\n$primary-bg: #eff8f2;\n$navbar-bg: #fafafa;\n$gray: #afafaf;\n$alert-red: #ef5870;\n$error-color: #fc4c4c;\n$error-text: #ea7474;\n$success-color: #43a047;\n$gray-fg: #535353;\n$gray-dc: #616161;\n$gray-title: #4e5665;\n$btn-primary-border: #e8e8e8;\n\n$unremarkable: #b9b9b9;\n\n$gray-bg-v2: #f4f5f7;\n$box-shadow: #f4f4f3;\n\n$severity-red: #fc4c4c;\n$severity-orange: #ffa83d;\n$severity-green: #36b081;\n\n$border-style: 1px solid #eceef1;\n$input-border-color: #dee1ec;\n\n$padding-base-vertical:     6px !default;\n$padding-base-horizontal:   12px !default;\n\n$border-radius-base:        4px !default;\n$line-height-base:        1.428571429 !default; // 20/14\n$pagination-bg:                        #fff !default;\n$pagination-border:                    #ddd !default;\n$pagination-hover-border:              #ddd !default;\n$pagination-active-color:              #fff !default;\n$pagination-disabled-bg:               #fff !default;\n$pagination-disabled-border:           #ddd !default;\n$pagination-active-border:           #337ab7 !default;\n$cursor-disabled:                not-allowed !default;\n","@import '../common/variables';\n\n/* Fixes for global styles */\nbody .modal-backdrop {\n    opacity: 0.4;\n    background: #4c4c4c;\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    z-index: 1040;\n\n    &.fade {\n        -webkit-transition: opacity 0.15s linear;\n        transition: opacity 0.15s linear;\n    }\n}\n\nbody.modal-open {\n    overflow: visible;\n}\n\n/* Fixes for plugin content */\n.i360-app.i360-plesk {\n    .add_item {\n        color: #333;\n    }\n\n    .pagination_nav {\n        ul.pagination {\n            margin-bottom: 16px;\n        }\n    }\n\n    .scanner-status-widget {\n        color: #333;\n    }\n\n    table > tbody {\n        tr {\n            td {\n                &.expander-column>div {\n                    transform: translateY(2px);\n                }\n            }\n\n            // Fix for Plesk v17.5\n            &:hover {\n                > th {\n                    box-shadow: none;\n                    background-color: #f8f8f8;\n                }\n\n                > td {\n                    box-shadow: none;\n                    background-color: $primary-bg;\n                }\n            }\n        }\n    }\n\n    .notification {\n        outline: none;\n    }\n\n    .search_block .cross_icon {\n        background-color: inherit;\n    }\n\n    .form-control {\n        &:focus {\n            color: $header-color;\n        }\n    }\n\n    input[type=radio]:disabled {\n        opacity: 0;\n    }\n}\n","@import '../common/variables';\n@import '../common/rtl';\n\n/* Fixes for plugin content */\n.i360-app.i360-directadmin {\n    margin: auto 10px;\n\n    * {\n        /* For limit widget, but it causes a problems with focused fields */\n        .cdk-overlay-pane {\n            &:not(.i360-modal-dialog-panel) {\n                bottom: 0 !important;  // forbid overflow at the bottom\n                height: auto;\n                max-height: fit-content;  // don't stretch to the bottom\n            }\n\n            pointer-events: none !important;\n            height: fit-content;  // Fix pop-up height for Chrome\n\n            > mat-dialog-container {\n                max-height: 80%;  // Fix pop-up height for Edge\n            }\n\n            > * {\n                pointer-events: auto;\n            }\n\n        }\n\n        .add-item-container {\n            textarea, input {\n                @include rtl-padding-left(14px);\n            }\n        }\n\n\n        font-size: inherit;\n        font-family: inherit;\n        line-height: inherit;\n\n        .i360-title {\n            font-size: 22px;\n        }\n\n        .list-tabs {\n            font-size: 16px;\n        }\n\n        h1 {\n            font-family: 'Open Sans', sans-serif;\n            font-size: 22px;\n        }\n\n        .title, .warning {\n            font-weight: normal;\n            color: inherit;\n        }\n\n        a {\n            cursor: pointer;\n        }\n\n        div.checkbox_block {\n            padding-top: 2px;\n        }\n\n        i360-error {\n            font-size: 11px;\n        }\n    }\n\n    .mat-select-placeholder {\n        line-height: 0.8;\n        color: transparent;\n    }\n\n    i360-tabs > div > a {\n        color: $header-color;\n        font-size: 16px;\n        font-family: Open Sans, sans-serif;\n\n        &:hover, &:focus {\n            color: $header-color;\n            text-decoration: none;\n        }\n    }\n\n    $width: 890;\n    $max-width: 1300;\n    $step: 30;\n\n    @while $width <= $max-width {\n         @media (min-width: #{$width}px) {\n            width: calc(#{$width}px - 100px);\n        }\n\n        $width: $width + $step;\n    }\n\n    @media (max-width: 789px) {\n        margin: auto 0;\n    }\n}\n\n// For Evolution theme where plugins are displayed inside of an iframe\n#iframe-container .i360-app.i360-directadmin {\n    height: auto;\n    min-height: 400px;\n    padding-bottom: 50px;\n}\n","\n@function hasAppParent($ampersand) {\n  @return nth(nth(&, 1), 1) == '.i360-app' or nth(nth(&, 1), 1) == ':not-i360-app-isolated';\n}\n\n@mixin dir($dir) {\n  // TODO: specify explicitly whether to use :host-context or not, because \"hasAppParent\" is imperfect\n  @if hasAppParent(&) {\n    .#{$dir} & {@content};\n  } @else {\n    :host-context(.#{$dir}) & {@content};\n  }\n}\n\n@mixin ltr {\n  @include dir(ltr) {@content};\n}\n\n@mixin rtl {\n  @include dir(rtl) {@content};\n}\n\n@mixin rtl-wrapper($ltr, $rtl, $value) {\n  @include ltr {#{$ltr}: $value};\n  @include rtl {#{$rtl}: $value};\n}\n\n@mixin rtl-wrapper-shorthand($cmd, $top, $right, $bottom, $left) {\n  @include ltr {#{$cmd}: $top $right $bottom $left};\n  @include rtl {#{$cmd}: $top $left $bottom $right};\n}\n\n@mixin rtl-margin($top, $right, $bottom, $left) {\n  @include rtl-wrapper-shorthand(margin, $top, $right, $bottom, $left);\n}\n\n@mixin rtl-margin-left($value) {\n  @include rtl-wrapper(margin-left, margin-right, $value);\n}\n\n@mixin rtl-margin-right($value) 